import tab from '@/components/wuc-tab/wuc-tab.vue'
import phone from '@/components/index/phone.vue'
import recommend from '@/components/index/recommend.vue'
import intellect from '@/components/index/intellect.vue'
import backTop from '@/components/common/back-top.vue'
import tabBar from '@/components/common/tab-bar.vue'
export default {
	data() {
		return {
		  TabCur: 0,
		  tabList: [
			  { name: '推荐' }, 
			  { name: '手机' }, 
			  { name: '智能' }, 
			  { name: '电视' },
			  { name: '笔记本' }, 
			  { name: '家电' }, 
			  { name: '生活周边' },
		  ],
		  isShowTop: false,
		  isShowNavLayer: false,
		  tabSelected: 'tab-selected',
		}
	},
	methods: {
		tabChange(index) {
			this.TabCur = index;
		},
		toUser() {
			uni.navigateTo({
				url: '../../pages/my/index'
			})
		},
		toSearch() {
			uni.navigateTo({
				url: '../../pages/search/index'
			})
		},
		toggleNavLayer() {
			this.isShowNavLayer = !this.isShowNavLayer;
		},
		toggleNavItem(index) {
			this.tabChange(index)
			this.toggleNavLayer()
		}
	},
	components: {
		tab,
		phone,
		recommend,
		intellect,
		backTop,
		tabBar,
	},
	onPageScroll(e) {
		if(e.scrollTop > 500) {
			this.isShowTop = true;
		}else{
			this.isShowTop = false;
		}
	},
}
